.button {
  --text: #ffffff;
  --background: #393e46d2;
  --background-opacity: 1;
  --handle-stroke: #E3FDFD;
  --handle-fill: rgba(255, 255, 255, 0.08);
  --handle-blur: 2;
  --drop-stroke: rgba(255, 255, 255, 0.2);
  --drop-fill: rgba(255, 255, 255, 0);
  --icon-stroke: #E3FDFD;
  --icon-rotate: 0;
  --icon-scale: 1;
  --icon-y: 0;
  --icon-offset: 16.8;
  --handle-drop-opacity: 0;
  --default-opacity: 1;
  --default-x: 0;
  --default-scale: 1;
  --progress-opacity: 0;
  --progress-scale: 0.75;
  --success-opacity: 0;
  --success-x: 0;
  --success-scale: 0.75;
  display: block;
  cursor: pointer;
  position: relative;
  text-align: center;
  outline: none;
  border: none;
  overflow: hidden;
  padding: 8px 0;
  margin: 0;
  width: 150px;
  line-height: 30px;
  font-family: inherit;
  font-weight: 600;
  border-radius: 5px;
  color: var(--text);
  background: var(--background);
  transform: scale(var(--button-scale, 1)) translateZ(0);
  transition: transform 0.15s;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
}
.button .handle,
.button .progress,
.button .success,
.button .drop {
  position: absolute;
  left: var(--left, auto);
  right: var(--right, auto);
  top: var(--top, auto);
}
.button .handle {
  backdrop-filter: blur(calc(var(--handle-blur) * 1px));
}
.button .handle,
.button .drop {
  --top: 8px;
  z-index: 1;
  border-radius: 11px;
  opacity: var(--handle-drop-opacity);
  pointer-events: var(--handle-drop-pointer, auto);
}
.button .handle svg,
.button .drop svg {
  display: block;
  width: var(--svg-size, 40px);
  height: var(--svg-size, 40px);
  stroke-width: 1px;
  stroke: var(--stroke, var(--handle-stroke));
  fill: var(--fill, var(--handle-fill));
}
.button .handle svg.background,
.button .drop svg.background {
  margin: -5px;
  opacity: var(--background-opacity);
}
.button .handle svg.icon,
.button .drop svg.icon {
  --svg-size: 20px;
  --fill: none;
  --stroke: var(--icon-stroke);
  position: absolute;
  left: 5px;
  top: 5px;
  stroke-width: 1.25;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 8.5;
  stroke-dashoffset: var(--icon-offset);
  transform: translateY(calc(var(--icon-y) * 1px)) rotate(calc(var(--icon-rotate) * 1deg)) scale(var(--icon-scale)) translateZ(0);
}
.button .handle {
  --left: 8px;
}
.button .drop {
  --right: 8px;
  --stroke: var(--drop-stroke);
  --fill: var(--drop-fill);
}
.button .default,
.button .progress,
.button .success {
  display: block;
  font-size: var(--font-size, 14px);
  opacity: var(--opacity, var(--default-opacity));
  transform: translateX(calc(var(--x, var(--default-x)) * 1px)) scale(var(--scale, var(--default-scale))) translateZ(0);
}
.button .progress,
.button .success {
  --left: 0;
  --right: 0;
  --top: 8px;
  --opacity: var(--progress-opacity);
}
.button .progress {
  --font-size: 11px;
  --opacity: var(--progress-opacity);
  --scale: var(--progress-scale);
  background-color: #393e4600;
    
  height: auto;
}
.button .success {
  --font-size: 14px;
  --opacity: var(--success-opacity);
  --x: var(--success-x);
  --scale: var(--success-scale);
}
.button:not(.active) {
  --handle-drop-pointer: none;
}
.button:not(.active):active {
  --button-scale: 0.975;
}
.active::before {
  width: 0 !important;
}